import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import TermsCondition from "../screens/terms-conditions";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const Terms = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Terms and Conditions", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <TermsCondition db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Terms and Conditions | Justtry Technologies"
headTitle="Terms and Conditions"
    description="Navigate the digital landscape confidently with JustTry Technologies' meticulous legal framework. Explore our Terms and Conditions, Privacy Policy, and User Agreement, crafted for your protection. Trust JustTry Technologies - where clarity meets compliance in the online realm."
    keywords="Legal terms, Terms of service, Privacy policy, User agreement, Terms and conditions, Legal compliance, Data protection, Contractual obligations, Agreement terms, User rights"
  ></Seo>
);

export default Terms;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
